<template>
	<div>
		<!-- <span id="date">Today</span> -->
	<el-row :gutter="12">
      <el-col :span="18" v-for="item in detail">
        <div v-if="item.is_newday == true && item.share == 1" class="entryDay">
          {{item.timestamp | getDay}}
        </div>
        <el-card shadow="hover" class="entryCard" v-if="item.share == 1">
          <div v-if="item.mood == 6" class="colorBar Orange"></div>
          <div v-if="item.mood == 5" class="colorBar Green"></div>
          <div v-if="item.mood == 4" class="colorBar Blue"></div>
          <div v-if="item.mood == 3" class="colorBar Wood"></div>
          <div v-if="item.mood == 2" class="colorBar Grey"></div>
          <div v-if="item.mood == 1" class="colorBar Black"></div>
          <div v-if="item.mood == 0" class="colorBar Dark"></div>
          <div class="entryContent">
            <div class="emojiImg">
              <img v-if="item.mood == 6" src="@/assets/emoji/emoji_supergood.png">
              <img v-if="item.mood == 5" src="@/assets/emoji/emoji_verygood.png">
              <img v-if="item.mood == 4" src="@/assets/emoji/emoji_good.png">
              <img v-if="item.mood == 3" src="@/assets/emoji/emoji_neutral.png">
              <img v-if="item.mood == 2" src="@/assets/emoji/emoji_bad.png">
              <img v-if="item.mood == 1" src="@/assets/emoji/emoji_verybad.png">
              <img v-if="item.mood == 0" src="@/assets/emoji/emoji_superbad.png">
            </div>
            <div class="detailText">
              <div class="entryWords">
                <div class='time'>{{item.timestamp | getTime}}</div>
                <div class="emotionClient">{{item.emotion_client}}</div>
                <div v-if="item.keyWords != null" class="keywords"><span>Keywords</span> {{item.keyWords}}</div>
                <div v-if="item.cognitive != null" class="distortions"><span>Distortions</span> {{item.cognitive}}</div>
              </div>
              <div class="entryAudio">
                <audio v-if="item.src != null " :src="item.src" controls></audio>
              </div>
              <div class="text"> 
                {{item.text}}
              </div> 
            </div>
            <div class="detailImg">
              <img v-if="item.notes != null " :src="item.notes">
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
	</div>
</template>

<script>
  import axios from 'axios'
  export default {
    data() {
      return {
        detail: '',
        time_restore: '',
      }
    },
    computed: {
      
    },
    methods: {
      color(mood) {
        if (mood == 0) {
          return 'dark'
        } else if (mood == 1) {
          return 'black';
        } else if (mood == 2) {
          return 'grey';
        } else if (mood == 3) {
          return '';
        } else if (mood == 4) {
          return 'blue';
        } else if (mood ==5) {
          return 'green';
        } else {
          return 'orange';
        }
      },
      changeDay(value) {
        const time = value.timestamp.split('T')[0]
        if (time != this.time_restore) {
          
          value.is_newday = true
        } else {
          value.is_newday = false
        }
        this.time_restore = time
        console.log(value)
      }
    },
    mounted() {
      const get_path = 'http://35.197.160.94:8094/patient/patientInfo?uidPatient=' + this.$store.state.patient_id
      axios.get(get_path)
      .then(response=>{
         console.log(response.data);
         this.detail=response.data.reverse();
         let time_restore = ''
         let index = 0
         this.detail.forEach(el => {
          if (index == 0) {
            time_restore = el.timestamp.split('T')[0]
            el.is_newday = true
            index += 1
          } else {
            if (time_restore == el.timestamp.split('T')[0]) {
              el.is_newday = false
            } else {
              time_restore = el.timestamp.split('T')[0]
              el.is_newday = true
            }
          }
          if (el.src != null) {
            let src_index = el.src.split('/').length
            el.src = '../music/' + el.src.split('/')[src_index - 1]
          }
          if (el.notes != null) {
            let note_index = el.notes.split('/').length
            el.notes = '../img/' + el.notes.split('/')[note_index - 1]
          }
         })
      })
      .catch(error=>{
          console.log(error);
          this.$message({
            message: "Network Error",
            type: "error"
          })
      })
    },
    filters: {
      getDay: function (value) {
        if (!value) return ''
        value = value.split('T')[0]
        const date = new Date(value)
        const day = value.split('-')[2]
        const year = value.split('-')[0]
        const mounth = date.toDateString().split(" ")[1]
        const format = day + ' ' + mounth + ' ' + year
        return format
      },
      getTime: function (value) {
        if (!value) return ''
        value = value.split('T')[1].split('.')[0]
        return value
      },
      getSrc: function (value) {
        if (!value) return ''
        const index = value.split('/').length
        value = '../music' + value.split('/')[index - 1]
      }
    },
  }
</script>

